import axios from "axios";

export const API_TYPES = {
    USER: "https://ef31-176-221-120-117.ngrok.io/User",
    DONE: "https://ef31-176-221-120-117.ngrok.io/Done",
    MATERIAL: "https://ef31-176-221-120-117.ngrok.io/Material",
    PLAN: "https://ef31-176-221-120-117.ngrok.io/Plan",
    PREWEIGHT: "https://ef31-176-221-120-117.ngrok.io/Preweight",
    RECIPE: "https://ef31-176-221-120-117.ngrok.io/Recipe",
    WAREHOUSE: "https://ef31-176-221-120-117.ngrok.io/Warehouse",
}

/**
 * 
 */
export default {

    request(url) {
        return {
            fetchAll: () => axios.get(url),
            fetchById: id => axios.get(url + id),
            create: (urlEnd,newRecord) => axios.post(url+urlEnd, newRecord),
            update: (id, updatedRecord) => axios.put(url + "/" + id, updatedRecord),
            delete: id => axios.delete(url + id),
            userLogin: user => axios.post(url + "/login", user),
            userRegister: (password, user) => axios.post(url + `/register?password=${password}`, user)
        }
    }
}
